@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;500;700&family=Permanent+Marker&display=swap');

/************************* color_plan ***************************/
$point_color_amber : #ffbf00;
$point_color_black : #000;
$main_color_flora_white : #FFFDF6;
$sub_color_free_speach_blue : #4136BF;
$color_white : #fff;

/************************* font_plan ***************************/
@mixin main_title() {
    font-size: 2.4rem;
    font-weight: 700;
}
@mixin sub_title() {
    font-size: 2rem;
    font-weight: 700;
}
$defalut_font_size : 1.6rem;
$small_font_size : 1.2rem;
$small_up_font_size : 1.4rem;

$font_noto_sans : 'Noto Sans KR', sans-serif;
$font_perment_marker : 'Permanent Marker';

/************************* gapï¼†padding ***************************/
@mixin gap($value) {
    margin: $value;
}
@mixin padding($value) {
    padding: $value;
}

/************************* background standard  ***************************/
@mixin background_contain {
    background-size: contain;
    background-repeat: no-repeat;
}
@mixin background_cover {
    background-size: cover;
    background-repeat: no-repeat;
}

/************************* item_size ***************************/
@mixin item_size ($width, $height) {
    width: $width;
    height: $height;
}

/************************* text_array ***************************/
@mixin text_array ($height) {
    text-align: center;
    line-height: $height;
}

/************************* center array  ***************************/
@mixin box_array {
    width: 1245px;
    margin: 0 auto;
}

/************************* defalut_settings ***************************/
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
    color: inherit;
}
html {
    font-size: 10px;
    background-color: $main_color_flora_white;
}
img {
    width: 100%;
    vertical-align: bottom;
}
.sub_title {
    @include sub_title;
    @include text_array(20px);
    margin: 20px 0;
    font-family: $font_perment_marker;
}

/************************* header_ex ***************************/
.header_ex {
    color: $color_white;
    background-color: $point_color_black;
    .header {
        position: relative;
        @include box_array;
        .logo {
            padding: 10px 0;
            a {
                display: block;
                @include item_size(175px, 50px);
                margin: 0 auto;
                background-image: url(../dist/images/main_logo.png);
                @include background_contain;
            }
            
        }
        .gnb {
            position: absolute;
            padding: 10px 0;
            top: 10px; right: 0;
            .gnb_list {
                overflow: hidden;
                li {
                    float: left;
                    margin-right: 15px;
                    &.icon {
                        margin-left: 10px;
                        a {
                            width: 20px; height: 20px;
                            background-image: url(../dist/images/search_icon.png);
                            @include background_cover;
                        }
                    }
                    &.cart {
                        a {
                            width: 30px; height: 30px;
                            background-image: url(../dist/images/cart_icon.png);
                            @include background_cover;
                            transform: translateY(-5px);
                        }
                    }
                    &:last-child {
                        margin-right: 0;
                    }
                    a {
                        display: block;
                        font-size: $small_font_size;
                        font-family: $font_noto_sans;
                    }
                }
            }
        }
    }
}

/************************* lnb_ex ***************************/
.lnb_ex {
    background-color: $point_color_black;
    .lnb {
        display: flex;
        @include box_array;
        height: 50px;
        justify-content: space-between;
        align-items: center;
        .main_menu_list {
            display: flex;
            justify-content: space-between;
            align-items: center;
            li {    
                margin-right: 50px;
                a {
                    display: block;
                    @include main_title;
                    letter-spacing: -0.8px;
                    color: $color_white;
                }
            }
        }
        .real_time_keyward_frame {
            @include item_size(120px, 20px);
            .keyward_list {
                overflow: hidden;
                height: 20px;
                li {
                    position: relative;
                    margin-left: 20px;
                    @include item_size(100px, 20px);
                    background-color: #fff;
                    a {
                        display: block;
                        padding-left: 10px;
                        line-height: 20px;
                        font-size: $small_up_font_size;
                        font-family: $font_noto_sans;
                    }
                    &:nth-child(1)::after {
                        content: "1";
                        position: absolute;
                        width: 20px; height: 20px;
                        top:0; left: -20px;
                        font-size: $small_font_size;
                        color: $color_white;
                        background-color: $sub_color_free_speach_blue;
                        @include text_array(20px);
                    }
                }
            }
        }
    }
}

/************************* visual_main ***************************/
.visual_main {
    background-color: red;
    height: 400px;
    margin-bottom: 40px;
}

/************************* product_container ***************************/
.product_container {
    @include box_array;
    position: relative;
    .btn {
        position: absolute;
        top: 25%; 
        transform: translateY(-25%);
        font-size: 50px;
        z-index: 9999;
        cursor: pointer;
        transition: all 0.2s;
        user-select: none;
        &:hover{
            color: $point_color_amber;
        }
    }
    .prev_btn {
        left: -80px;
    }
    .next_btn {
        right: -80px;
    }
    .product {
        position: relative;
        @include item_size(1245px, 340px);
        overflow: hidden;
        margin-bottom: 40px;
        .product_frame {
            position: absolute;
            left: 0;
            @include item_size(2505px, 300px);
            overflow: hidden;
            transition: all 0.2s;
            .product_list {
                float: left;
                @include item_size(1245px, 300px);
                li {
                    float: left;
                    margin-right: 15px;
                    @include item_size(300px, 300px);
                    &:last-child {
                        margin-right: 0;
                    }
                    a {
                        position: relative;
                        display: block;
                        @include item_size(300px, 300px);
                        &::after {
                            content: "";
                            position: absolute;
                            z-index: 1;
                            top: 0; left: 0;
                            @include item_size(300px, 300px);
                            opacity: 0;
                            transition: all 0.3s;
                        }
                        &:hover {
                            span {
                                opacity: 1;
                            }
                            &::after {
                                opacity: 10;
                                background-color: rgba(0,0,0,0.2);
                            }
                        }
                        img {
                            @include item_size(100%, 100%);
                        }
                        span {
                            position: absolute;
                            display: block;
                            opacity: 0;
                            width: 100%;
                            text-align: center;
                            z-index: 10;
                            font-size: $small_font_size;
                            color: $color_white;
                            &:nth-child(2) {
                                top: 120px;
                                left: 50%;
                                transform: translateX(-50%);
                            }
                            &:nth-child(3) {
                                top: 140px;
                                left: 50%;
                                transform: translateX(-50%);
                            }
                        }
                    }
                }
            }
        }
    }
}

/************************* main_issue_contents ***************************/
.main_issue_contents {
    .look_book {
        @include box_array;
        margin-bottom: 40px;
        .look_book_thumnail {
            display: flex;
            margin-bottom: 15px;
            >div {
                @include item_size(615px, 615px);
                background-color: red;
                margin-right: 15px;
            }
            .big_look_book {
            }
            .video_n_look_book {
                margin-right: 0;
                .video_film {
                    height: 300px;
                    background-color: blue;
                    margin-bottom: 15px;
                }
                .small_look_book {
                    display: flex;
                    >div {
                        @include item_size(300px, 300px);
                        margin-right: 15px;
                        background-color: black;
                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
            }
        }
        .look_book_view {
            height: 670px;
            background-color: red;
            margin-bottom: 15px;
        }
    }
    .mds_pick {
        @include box_array;
        margin-bottom: 40px;
        ul {
            display: flex;
            .acodion_on {
                position: relative;
                @include item_size(615px, 400px);
                background-color: blue;
                .product_info {
                    position: absolute;
                    height: 400px;
                    right: 0;
                    z-index: 10;
                    width: 280px;
                    background-color: green;
                }
            }
            li {
                @include item_size(195px, 400px);
                background-color: black;
                margin-right: 15px;
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }
}
.insta {
    @include box_array;
    margin-bottom: 40px;
    ul {
        display: flex;
        li {
            a {
                display: block;
                @include item_size(300px, 300px);
                margin-right: 15px;
                background-color: blue;
            }
        }
    }
}

.site_info {
    .info_list {
        @include box_array;
        text-align: center;
        font-size: 0;
        margin-bottom: 30px;
        li {
            display: inline-block;
            font-size: $defalut_font_size;
            font-family: $font_noto_sans;
            width: 20%;
            border-right: 1px solid #bbbbbb;
            &:last-child {
                border: none;
            }
        }
    }
}

/************************* footer_ex ***************************/
.footer_ex {
    background-color: $point_color_black;
    color: $color_white;
    .footer {
        @include box_array;
        a {
            display: block;
        }
        .footer_top {
            display: flex;
            justify-content: space-between;
            .footer_left {
                width: 75%;
                .guide_list {
                    overflow: hidden;
                    margin: 20px 0 40px;
                    li {
                        float: left;
                        margin-right: 30px;
                        a {
                            font-size: $small_font_size;
                        }
                    }
                }
                .business_info_list {
                    margin-bottom: 40px;
                    li {
                        font-size: $small_up_font_size;
                        letter-spacing: -0.8px;
                        margin-bottom: 15px;
                    }
                }
            }
            .footer_right {
                margin-top: 10px;
                width: 25%;
                a {
                    display: block;
                }
                .sns_list {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 20px;
                    li {
                        a {
                            font-size: 30px;
                        }
                    }
                }
                .contact_info_list {
                    li {
                        a {
                            overflow: hidden;
                            font-size: $small_up_font_size;
                            margin-bottom: 15px;
                            span {
                                &:first-child {
                                    float: left;
                                    font-size: $defalut_font_size;
                                    font-weight: 700;
                                }   
                                &:last-child {
                                    float: right;
                                }
                            }
                        }
                    }
                }
            }
        }
        .notice {
            display: flex;
            padding-bottom: 40px;
            li {
                margin-right: 20px;
                a {
                    font-size: $small_up_font_size;
                    letter-spacing: -0.8px;
                }
            }
        }
    }
}